import { createRouter, createWebHashHistory, RouteRecordRaw } from "vue-router";

const Home = () => import(/* webpackChunkName: "home" */ "@/views/Home.vue");
const Essay = () => import(/* webpackChunkName: "essay" */ "@/views/Essay.vue");

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "Home",
    redirect: "/notes/有关笔记.html",
    component: Home
  },
  {
    path: "/notes/:noteName",
    name: "Notes",
    component: Home
  },
  {
    path: "/essays",
    name: "EssaysIndex",
    redirect: "/essays/index.html",
    component: Essay
  },
  {
    path: "/essays/:essayName",
    name: "Essays",
    component: Essay
  }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router;
